import React from 'react';
import Latex from 'react-latex';
export default function ExatoFisher(){ return(<>

<h2>
    Teste Exato de Fisher
</h2>

A ideia é verificar se existe diferença entre dois tratamentos

<Latex displayMode={true}>{'$$'+
'\\begin{array}{c|c|c|c} '+
'&Sucesso&Fracasso&Total\\\\ '+
'\\hline '+
'\\text{Tratamento 1}&a&b&n_{1} \\\\ '+
'\\text{Tratamento 2}&c&d&n_{2} \\\\ '+
'\\hline '+
'Total&s&f&n '+
'\\end{array}'
+'$$'}</Latex>

<h3>
    Hipóteses do Teste
</h3>

Seja, <Latex>{"$p_{1}$"}</Latex> a probabilidade de sucesso no tratamento 1 e <Latex>{"$p_{2}$"}</Latex> a probabilidade de sucesso no tratamento 2

<Latex displayMode={true}>{'$$'+
'\\begin{cases} '+
'h_0 : {p}_{1} = {p}_{2} \\\\ '+
'h_0 : {p}_{1} \\mathrel{\\char`≠} {p}_{2}\\text{, ou, }{p}_{1} > {p}_{2}\\text{, ou, } {p}_{1} < {p}_{2}'+
'\\end{cases}'
+'$$'}</Latex>


<h3>
    Estatística do Teste
</h3>

A estatística do teste é a quantidade de sucessos no tratamento 1 condicionada à quantidade de sucessos nos dois tratamentos. Dado que tive a+c sucessos qual a probabilidade de que eu tenha a sucessos no tratamento 1? Sob <Latex>{"$h_{0}$"}</Latex>, <Latex>{"$p_{1}=p_{2}=p$"}</Latex>, então <Latex>{"$\\frac{a}{n_{1}}$"}</Latex> e <Latex>{"$$"}</Latex> deveriam ser próximos.

<Latex displayMode={true}>{'$$'+
'X=\\frac{a}{n_{1}} - \\frac{c}{n_{2}}\\sim\\text{Hipergeométrica}=\\frac{\\binom{n_{1}}{a}\\binom{n_{1}}{c}}{\\binom{n_{1}+n_{2}}{a+c}}'
+'$$'}</Latex>

<b>Prova:</b> Sob <Latex>{"$h_{0}$"}</Latex>, <Latex>{"$p_{1}=p_{2}=p$"}</Latex>, então <Latex>{"$A\\sim Binomial(n_{1}, p)$"}</Latex> e <Latex>{"$C\\sim Binomial(n_{2}, p)$"}</Latex> 

<Latex displayMode={true}>{'$$'+
'P(A=a|S=a+c)= \\frac{P(A=a \\cap S=a+c)}{ P(S=a+c) }= \\frac{P(A=a \\cap C=a-s)}{ P(S=a+c) }='+
'\\frac{P(A=a)P(C=a-s)}{P(C=a+c)} = '
+'$$'}</Latex>

<Latex displayMode={true}>{'$$'+
'\\frac{\\binom{n_{1}}{a}p^{a}(1-p)^{n_{1}-a}\\binom{n_{2}}{a-s}p^{a-s}(1-p)^{n_{2}-a+s}}{\\binom{n}{a+c}p^{a+c}(1-p)^{n-a-c}} \\stackrel{c=a-s}{=}'+
'\\frac{\\binom{n_{1}}{a}\\binom{n_{1}}{c}}{\\binom{n_{1}+n_{2}}{a+c}}'
+'$$'}</Latex>

</>)}